<template>
	<view>
		<view class="hot-activity-body" @click="onClick()">
			<view class="hot-ranking" >
				<view class="ranking-content"
				:style="{
					 backgroundColor:ranking==1? '#EF4F4A'
									:ranking==2? '#FD930D'
									:ranking==3? '#F0C174':'#56CDBA'
				}"
				>
					<view v-if="ranking<=3" class= 'iconfont icon-remen' :style="{
									fontSize: '12px',
									color: '#fff'
								}" />
					<view v-else class="ranking-text">
						{{ranking}}
					</view>
				</view>                                                                                                                           
			</view>
			<view class="hot-activity-content">
				<view class="hot-activity-info">
					<view class="hot-activity-text-content">
						<view class="text-info">{{articleText}}</view>
						<view class="hot-activity-footer">
							<!-- 喜欢按钮 -->
							<view class="hot-activity-footer-item" @click.stop="onLikeButtonClick()">
								<view class= 'iconfont icon-heart' :style="{
												fontSize: '15px'
											}" />
								<view class="footer-item-number">{{ articleItem.likeSum }}</view>
							</view>
							<!-- 评论按钮 -->
							<view class="hot-activity-footer-item">
								<view class="iconfont icon-pinglun" :style="{ fontSize: '15px'}" />
								<view class="footer-item-number">{{ articleItem.commentSum }}</view>
							</view>
							
						</view>
					</view>
					<view v-if="articleItem.pic.length>0" >
						<view v-if="articleItem.video == 1" class="hot-activity-image">
							<video class="image-item"  :src="articleItem.pic[0]" :id="`video_${articleItem.id}`" initial-time="0" controls></video>
						</view>
						<view v-else class="hot-activity-image">
							<image class="image-item" :lazy-load="true" mode="aspectFill" :src="articleItem.pic[0]"  />
						</view>
					</view>
				</view>
				
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name:"hot-activity-item",
		props: {
			articleItem: {
				type: Object,
				required: true
			},
			ranking:{
				type:Number,
				required:true
			}
			},
		data() {
			return {
				articleText:''
				
				};
			},
		beforeMount(){
			this.initText()
		},
		methods:{
			/**
			 * @description 点击主体
			 */
			onClick(){
				this.$emit('onClick')
			},
			/**
			 * @description 初始化文字格式
			 */
			initText() {
				let text = this.articleItem.text
				let maxLength = 100
				//一个换成换算成20个字符串，最后只能有100个字符位，多了就要收起
				let line_breaks = 0
				let transformTextLength =0
				let textMatch = text.match(/\n/ig)
				if (textMatch) {
					line_breaks = textMatch.length
				}
				if (line_breaks <= 0) {
					transformTextLength = text.length
				} else {
					transformTextLength = text.length + line_breaks * 19
					if (transformTextLength > 100) {
						let times = 1
						let first_break = text.indexOf('\n')
						let temp_break = first_break //临时长度 将一个换行换成20长度
						let real_break = first_break //真实长度，一个换行为一个长度
						let temp_text = text
						while (temp_break < 100) {
							temp_text = text.slice(real_break + 1)
							if (temp_text.indexOf('\n') == -1) {
								break
							}
							temp_break = temp_break + 20 + temp_text.indexOf('\n')
							real_break = real_break + 1 + temp_text.indexOf('\n')
							times = times + 1
						}
						maxLength = real_break
					}
				}
				let showAll = transformTextLength <= 100 //换算后的文字长度小于100则转换成真实长度
				if(showAll){
					this.articleText = this.articleItem.text
				}else{
					this.articleText = this.articleItem.text.slice(0, maxLength) + '...'
				}
		}
		}
	}
</script>

<style lang="scss">
	.hot-activity-body{
		margin: 1% 0;
		padding: 30rpx 20rpx;
		display: flex;
		background-color: #FFFFFF;
	}
	.hot-ranking{
		width: 8%;
		text-align: center;
		font-size: smaller;
		color: #FFFFFF;
	}
	.ranking-content{
		min-height: 30rpx;
		min-width: 30rpx;
		padding: 5rpx;
		font-size: small;
		width: fit-content;
		border-radius: 20rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
	}
	.hot-activity-content{
		font-size: small;
		color: #3b3b3b;
		width: 92%;
		position: relative;
		.hot-activity-info{
			display: flex;
			.hot-activity-text-content{
				flex-grow: 1;
				text-align: left;
				display: flex;
				flex-direction: column;
				min-height: 150rpx;
				.text-info{
					flex-grow: 1;
				}
			}
			.hot-activity-image{
				margin-left: 20rpx;
				height: 130rpx;
				width: 200rpx;
				border-radius: 10rpx;
				overflow: hidden;
			}
			.image-item{
				height: 150rpx;
				width: 250rpx;
			}
		}
		.hot-activity-footer{
			display: flex;
			align-items: left;
			color: #808080;
			font-size: smaller;
			.hot-activity-footer-item{
				width: 100rpx;
				text-align: left;
				display: flex;
			}
			.footer-item-number{
				margin-left: 10rpx;
			}
		}
	}
</style>
